.main-block {
  width: 600px;
  height: 500px;
  margin: 0 auto;
  position: relative; }

.header {
  width: 600px;
  height: 400px;
  display: flex;
  overflow: hidden;
  font-size: 192px;
  text-align: center;
  font-weight: bold;
  font-family: sans-serif;
  border: 5px solid grey; }

.buttons {
  width: 600px;
  height: 100px;
  position: absolute;
  bottom: 0;
  left: 0; }

.btn {
  width: 70px;
  background: transparent;
  border-radius: 10px;
  border: none;
  padding: 0;
  position: absolute;
  top: -250px;
  right: -90px;
  cursor: pointer;
  color: grey;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 92px; }

.btn:hover {
  transition: color 0.5s ease;
  color: #000; }

