.move {
  transform: translateX(-200px); }

.checkpoints {
  width: 120px;
  display: flex;
  margin: 50px auto; }

.checkPoint {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin-right: 12px;
  cursor: pointer;
  position: relative; }

.checkPoint:after {
  content: "";
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid grey;
  position: absolute;
  top: -3px;
  left: -3px; }

.checkPoint.active {
  background: grey; }
